<template>
  <div class="container">
    <h1><strong>HTTP Dummy Responser</strong></h1>
    <span class="subtitle">Mock HTTP responses to test REST API</span>
    <div class="box">
      <span class="small-title"><strong>Usage #1: Echo</strong></span>
      <span>The response reflects your body with GET, POST, PUT, DELETE</span>
      <div>
        <div class="raw">&gt; POST <span class="fake-link">https://httpdummyresponser.herokuapp.com/echo</span><br />
          &gt; Content-Type: application/json; charset=UTF-8<br />
          {"hello": "world"}
        </div>
        <div class="raw">&lt; HTTP/1.1 200 Ok<br />
          &lt; Content-Type: application/json; charset=UTF-8<br />
          {"hello": "world"}
        </div>
      </div>
      <span>You can manipulate HTTP status and custom headers</span>
      <div class="raw">&gt; PUT <span class="fake-link">https://httpdummyresponser.herokuapp.com/echo?dummy-status=400&amp;echo-hdr=X-API-AUTH</span><br />
        &gt; X-API-AUTH: BLAHBLAH<br />
        &gt; X-CUSTOM-ONE: ONE<br />
        &gt; Content-Type: application/json; charset=UTF-8 <br />
        {"hello": "world"}
      </div>
      <div class="raw">&lt; HTTP/1.1 400 Bad Request<br />
        &lt; Content-Type: application/json; charset=UTF-8<br />
        &lt; X-API-AUTH: BLAHBLAH<br />
        {"hello": "world"}
      </div>
    </div>
    <div class="box">
      <span class="small-title"><strong>Usage #2: Custom Response</strong></span>
      <span>You can create your own response</span>
      <div class="dropdown">
        Status Code
        <el-select v-model="statusCodeSelected" filterable placeholder="Select" size="mini">
          <el-option
            v-for="item in statusCodes"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="dropdown">Content Type
        <el-select v-model="contentTypeSelected" filterable placeholder="Select" size="mini">
          <el-option
            v-for="item in contentTypes"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="dropdown">Charset
        <el-select v-model="charsetSelected" filterable placeholder="Select" size="mini">
          <el-option
            v-for="item in charsets"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div>
        <el-row v-for="(item, index) in customHeaders" :key="index">
          <el-input placeholder="key" style="max-width:250px;"
          size="mini" v-model="item.key" v-bind:value="item.key">
          </el-input>
          <el-input placeholder="value" style="max-width:250px;" size="mini"
          v-model="item.value" v-bind:value="item.value">
          </el-input>
          <el-button type="info" icon="el-icon-plus" size="mini" circle
          v-on:click="addHeader(index)">
          </el-button>
        </el-row>
      </div>
      <div class="dropdown">Body
        <el-input type="textarea" :rows="8" placeholder="Please input" v-model="textarea">
        </el-input>
      </div>
      <div>
        <el-button type="primary" v-on:click="handleSubmit($event)" :loading="isLoading">
        Submit
        </el-button>
      </div>
      <div style="margin-top: 20px;">
        <span class="success-link" v-html="msg" v-if="msg != ''"></span>
      </div>
      <span>You can manipulate status code with 'dummy-status' parameter</span>
      <div class="raw">
        &gt; GET <span class="fake-link">https://httpdummyresponser.herokuapp.com/v1/5abda9b6be9f7500044b47fc</span><br />
        &gt; Content-Type: application/json; charset=UTF-8 <br />
        {"hello": "world"}<br /><br />
        &lt; HTTP/1.1 200 OK<br />
        &lt; Content-Type: application/json; charset=UTF-8<br />
        {"hello": "world"}<br /><br />
        &gt; GET <span class="fake-link">https://httpdummyresponser.herokuapp.com/v1/5abda9b6be9f7500044b47fc?dummy-status=400</span><br />
        &gt; Content-Type: application/json; charset=UTF-8 <br />
        {"hello": "world"}<br /><br />
        &lt; HTTP/1.1 400 Bad Request<br />
        &lt; Content-Type: application/json; charset=UTF-8<br />
        {"hello": "world"}
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'MainOne',
  data() {
    return {
      msg: '',
      isLoading: false,
      contentTypeSelected: '',
      charsetSelected: '',
      statusCodeSelected: '',
      textarea: '',
      customHeaders: [
        { key: '', value: '' },
      ],
      contentTypes: [
        { label: 'application/json', value: 'application/json' },
        { label: 'application/x-www-form-urlencoded', value: 'application/x-www-form-urlencoded' },
        { label: 'application/xhtml+xml', value: 'application/xhtml+xml' },
        { label: 'application/xmlmultipart/form-data', value: 'application/xmlmultipart/form-data' },
        { label: 'text/css', value: 'text/css' },
        { label: 'text/csv', value: 'text/csv' },
        { label: 'text/html', value: 'text/html' },
        { label: 'text/json', value: 'text/json' },
        { label: 'text/plain', value: 'text/plain' },
        { label: 'text/xml', value: 'text/xml' },
      ],
      charsets: [
        { label: 'utf-8', value: 'utf-8' },
        { label: 'ISO8859-1', value: 'ISO8859-1' },
        { label: 'utf-16', value: 'utf-16' },
      ],
      statusCodes: [
        { label: '100 Continue', value: 100 },
        { label: '101 Switching Protocols', value: 101 },
        { label: '102 Processing', value: 102 },
        { label: '103 Early Hints', value: 103 },
        { label: '200 OK', value: 200 },
        { label: '201 Created', value: 201 },
        { label: '202 Accepted', value: 202 },
        { label: '203 Non-Authoritative Information', value: 203 },
        { label: '204 No Content', value: 204 },
        { label: '205 Reset Content', value: 205 },
        { label: '206 Partial Content', value: 206 },
        { label: '207 Multi-Status', value: 207 },
        { label: '208 Already Reported', value: 208 },
        { label: '226 IM Used300 Multiple Choices', value: 226 },
        { label: '301 Moved Permanently', value: 301 },
        { label: '302 Found', value: 302 },
        { label: '303 See Other', value: 303 },
        { label: '304 Not Modified', value: 304 },
        { label: '305 Use Proxy', value: 305 },
        { label: '306 Switch Proxy', value: 306 },
        { label: '307 Temporary Redirect', value: 307 },
        { label: '308 Permanent Redirect', value: 308 },
        { label: '400 Bad Request', value: 400 },
        { label: '401 Unauthorized', value: 401 },
        { label: '402 Payment Required', value: 402 },
        { label: '403 Forbidden', value: 403 },
        { label: '404 Not Found', value: 404 },
        { label: '405 Method Not Allowed', value: 405 },
        { label: '406 Not Acceptable', value: 406 },
        { label: '407 Proxy Authentication Required', value: 407 },
        { label: '408 Request Timeout', value: 408 },
        { label: '409 Conflict', value: 409 },
        { label: '410 Gone', value: 410 },
        { label: '411 Length Required', value: 411 },
        { label: '412 Precondition Failed', value: 412 },
        { label: '413 Request Entity Too Large', value: 413 },
        { label: '414 Request-URI Too Long', value: 414 },
        { label: '415 Unsupported Media Type', value: 415 },
        { label: '416 Requested Range Not Satisfiable', value: 416 },
        { label: '417 Expectation Failed', value: 417 },
        { label: '418 I\'m a teapot', value: 418 },
        { label: '420 Enhance Your Calm', value: 420 },
        { label: '422 Unprocessable Entity', value: 422 },
        { label: '423 Locked', value: 423 },
        { label: '424 Failed Dependency', value: 424 },
        { label: '425 Unordered Collection', value: 425 },
        { label: '426 Upgrade Required', value: 426 },
        { label: '428 Precondition Required', value: 428 },
        { label: '429 Too Many Requests', value: 429 },
        { label: '431 Request Header Fields Too Large', value: 431 },
        { label: '500 Internal Server Error', value: 500 },
        { label: '501 Not Implemented', value: 501 },
        { label: '502 Bad Gateway', value: 502 },
        { label: '503 Service Unavailable', value: 503 },
        { label: '504 Gateway Timeout', value: 504 },
        { label: '505 HTTP Version Not Supported', value: 505 },
        { label: '506 Variant Also Negotiates', value: 506 },
        { label: '507 Insufficient Storage', value: 507 },
        { label: '509 Bandwidth Limit Exceeded', value: 509 },
        { label: '510 Not Extended', value: 510 },
        { label: '511 Network Authentication Required', value: 511 },
      ],
    };
  },
  mounted() {
    this.contentTypeSelected = this.contentTypes[0].value;
    this.statusCodeSelected = this.statusCodes[4].value;
    this.charsetSelected = this.charsets[0].value;
  },
  methods: {
    addHeader(idx) {
      this.customHeaders.splice(idx + 1, 0, { key: '', value: '' });
    },
    handleSubmit() {
      this.msg = '';
      this.isLoading = true;

      const hdrs = {};
      for (let i = 0; i < this.customHeaders.length; i += 1) {
        if (this.customHeaders[i].key !== '') {
          hdrs[this.customHeaders[i].key] = this.customHeaders[i].value;
        }
      }

      axios.post('https://httpdummyresponser.herokuapp.com/create', {
        content: this.textarea,
        charset: this.charsetSelected,
        status: this.statusCodeSelected,
        content_type: this.contentTypeSelected,
        headers: hdrs,
      })
        .then((response) => {
          this.msg = `Mock URL is created: <a href="${response.data.url}" style="color:#42b983">${response.data.url}</a>`;
          this.isLoading = false;
        })
        .catch(() => {
          this.msg = 'An error while creating URL. Try again or make an issue.';
          this.isLoading = false;
        });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

a {
  color: #42b983;
}

.fake-link {
  color: #42b983;
}

.raw {
  display: block;
  padding: 10px;
  font-size: 14px;
  line-height: 20px;
  background-color: #f5f5f5;
  border: 1px solid rgba(0,0,0,0.2);
  border-radius: 4px;
  text-align: left;
  margin: 10px 0px;
}
.subtitle {
  font-size: 18px;
  font-weight: 200px;
}

.container {
  width: 50%;
  margin-left: 10%;
  margin-right:10%;
  margin-left: auto;
  margin-right: auto;
}

.box {
  border-radius: 2px;
  text-align: left;
  border: 1px solid rgba(0,0,0,0.5);
  padding: 10px;
  margin : 10px 0px;
}

.small-title {
  font-size: 20px;
}

.dropdown {
  margin-bottom: 10px;
}

.success-link {
  background-color: rgb(202, 202, 202);
  margin-left:20px;
  padding: 5px;
  border-radius: 4px;
  font-size: 14px;
}

.is-circle {
  padding: 2px;
}
</style>



